import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import {withRouter} from 'react-router-dom';
import Button from '@material-ui/core/Button';
import CardActions from '@material-ui/core/CardActions';
import Grid from "@material-ui/core/Grid";
import ButtonBase from "@material-ui/core/ButtonBase";
import Booking from './Booking';
import NumberFormat from 'react-number-format';
import Moment from 'react-moment';
import useStyles from '../Utils/style';

const SimpleCard = (props) => {
  const classes = useStyles();
  const {trip, tripEditing} = props;
  return (
    <Card className={classes.card}>
      <CardContent>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="flex-start"
        >
          <Grid item md={6}>
            <Typography variant="h5" className={classes.title} gutterBottom>
              {trip.fromStation.province} - {trip.toStation.province}
            </Typography>
            <Typography variant="h6" gutterBottom className={classes.link}>
              {trip.company.name}
            </Typography>
            <ButtonBase className={classes.image}>
              <img
                className={classes.img}
                alt="complex"
                src={
                  trip.company.image
                    ? trip.company.image
                    : "https://upload.wikimedia.org/wikipedia/vi/1/1a/Nh%C3%A0_ga_b%E1%BA%BFn_xe_mi%E1%BB%81n_%C4%90%C3%B4ng.JPG"
                }
              />
            </ButtonBase>
          </Grid>
          <Grid item md={6}>
          <Typography variant="h6" color="primary">
              Gi v :{" "}
              <NumberFormat
                value={trip.price}
                displayType={"text"}
                thousandSeparator={true}
                suffix={" VND"}
              />
            </Typography>
            <Typography color="textPrimary" className={classes.link}>
              im xut pht : {trip.fromStation.name}
            </Typography>
            <Typography color="textPrimary" className={classes.link}>
              im n : {trip.toStation.name}
            </Typography>
            <Typography color="textPrimary">
              Gi khi hnh:{" "}
              <Moment format=" HH:mm DD/MM/YYYY">{trip.startTime}</Moment>
            </Typography>
            <Typography color="textSecondary">{trip.carType}</Typography>
            <Typography color="textSecondary">
              S gh trng: {trip.seats.filter(e => !e.isBooked).length} /{" "}
              {trip.seats.length}
            </Typography>
          </Grid>
        </Grid>
        <CardActions>
          {tripEditing._id === trip._id ? (
            <Booking tripId={trip._id} />
          ) : (
            <Button
              size="small"
              variant="contained"
              style={{ marginLeft: "auto" }}
              onClick={() => props.getTripById(trip._id)}
            >
              {trip.seats.filter(e => !e.isBooked).length === 0
                ? "Ht gh "
                : "Chn gh"}
            </Button>
          )}
        </CardActions>
      </CardContent>
    </Card>
  );
}

export default withRouter(SimpleCard);